* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  width: 100vw;
  background: #18191f;
  overflow: hidden;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}
.header {
    position: absolute;
    top: 10%;
    text-align: center;
    width: 100%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:white;
    font-size: 27px;
    font-weight: 700;
}
.status{
    position: absolute;
    bottom:  20%;
    text-align: center;
    width: 100%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:#00ff00;
    font-size: 35px;
    font-weight: 700;
    animation: status 1s infinite;
}
#shop__hold{
  font-size: 8em;
  background-color: #18191f;
  color: #fff;
  box-shadow:
    0 0 10px #ffffff,
    0 0 20px #ffffff,
    0 0 40px #ffffff,
    0 0 80px #ffffff,
    inset 0 0 10px #ffffff;
  border-radius: 29px;
  padding: 11px 19px;
  margin: 0 40px;
  text-shadow:
    0 0 10px #ffffff,
    0 0 20px #ffffff,
    0 0 40px #ffffff;
}

#shop__open{
  font-size: 8em;
  background-color: #18191f;
  color: #fff;
  box-shadow:
    0 0 10px #00ff00,
    0 0 20px #00ff00,
    0 0 40px #00ff00,
    0 0 80px #00ff00,
    inset 0 0 10px #00ff00;
  border-radius: 29px;
  padding: 11px 19px;
  margin: 0 40px;
  text-shadow:
    0 0 10px #00ff00,
    0 0 20px #00ff00,
    0 0 40px #00ff00;
}

#shop__close{
  font-size: 8em;
  background-color: #18191f;
  color: #fff;
  box-shadow:
    0 0 10px #ff4242,
    0 0 20px #ff4242,
    0 0 40px #ff4242,
    0 0 80px #ff4242,
    inset 0 0 10px #ff4242,;
  border-radius: 29px;
  padding: 11px 19px;
  margin: 0 40px;
  text-shadow:
    0 0 10px #ff4242,
    0 0 20px #ff4242,
    0 0 40px #ff4242;
}


@keyframes status{
    0%{
        scale: 1;
    }
    60%{
        scale: 1.2;
    }
    100%{
        scale: 1;
    }
}

.status_hold{
  color: white;
}
.status_open{
  color: #00ff00;
}
.status_close{
  color: #ff4242;
  
}