/* Apply existing global styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  background: #18191f;
  overflow: hidden;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
  color: #00ff00;
}

.shop-control__green {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1e1f26;
  padding: 30px;
  border-radius: 20px;
  box-shadow:
    0 0 10px #00ff00,
    0 0 20px #00ff00,
    0 0 30px #00ff00;
}
.shop-control__red {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1e1f26;
  padding: 30px;
  border-radius: 20px;
  box-shadow:
    0 0 10px #ff4242,
    0 0 20px #ff4242,
    0 0 30px #ff4242;
}
.key-prompt{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1e1f26;
  padding: 30px;
  border-radius: 20px;
  box-shadow:
    0 0 10px white,
    0 0 20px white,
    0 0 30px white;
}
.shop-control .green {
  margin-bottom: 20px;
  font-size: 28px;
  color: #00ff00;
}
.shop-control .red {
  margin-bottom: 20px;
  font-size: 28px;
  color: #ff4242;
}
.key-prompt h2{
  margin-bottom: 20px;
  font-size: 28px;
  color: white;
}

.key-prompt input {
  padding: 10px;
  font-size: 18px;
  border-radius: 10px;
  border: none;
  outline: none;
  margin-bottom: 20px;
  background-color: #2a2b33;
  color: white;
  box-shadow: inset 0 0 5px white;
}

.key-prompt input::placeholder {
  color: rgba(255, 255, 255, 0.671);
}

.key-prompt button {
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.959);
  color: #18191f;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 5px white,
    0 0 10px white,
    0 0 20px white;
  transition: transform 0.2s;
}
.shop-control .green-button {
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  background-color: #00ff00;
  color: #18191f;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 5px #00ff00,
    0 0 10px #00ff00,
    0 0 20px #00ff00;
  transition: transform 0.2s;
}
.shop-control .red-button {
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 10px;
  background-color: #ff4242;
  color: #18191f;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 5px #ff4242,
    0 0 10px #ff4242,
    0 0 20px #ff4242;
  transition: transform 0.2s;
}

.key-prompt button:hover,
.shop-control button:hover {
  transform: scale(1.05);
}

.error {
  color: red;
  margin-top: 10px;
  font-size: 16px;
  font-weight: bold;
}
